.counterWrapper {
    display: flex;
    flex-flow: column;
    justify-content: space-evenly;
    align-items: center;
    border-color: white;
    border-style: solid;
    border-radius: 7px;
    border-width: 1px;
    width: 350px;
    height: 210px;
    background-color: #004643;

}

.counterValue {
    width: 320px;
    height: 64px;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;

    border-radius: 7px;
    border-style: solid;
    border-width: 1px;
    border-color: white;
    margin: 5px 0 5px 0;
    background-color: #f9bc60;
    color: black;
    font-size: medium;
    text-decoration: none;
}

.button {
    width: 128px;
    height: 48px;
    border-radius: 7px;
    border-style: solid;
    border-width: 1px;
    border-color: white;
    background-color: #f9bc60;
    color: black;
    font-size: medium;
}